.App {
  text-align: center;
  width: 100%;
  min-width: 600px;
  height:100vh;
}

.title {
	font-family: 'Press Start 2P', cursive;
}

.fires{
	height:9vw;
}

.monsters{
	height:9vw;
}

.header_section{
	background-color: rgb(44,62,80);
	padding: 1rem 0;
	display: -ms-flexbox;
	display: flex;
	color: white;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	min-width: 600px;
}

	.header_section_right{
		display: -ms-inline-flexbox;
		display: inline-flex;
	}

.footer_section{
	background-color: rgb(44,62,80);
	padding: 1rem 0;
	display: -ms-flexbox;
	display: flex;
	color: white;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	min-width: 600px;
}

	.footer_section_left{

	}

/*it contains board and control*/
.main_container{
  display: -ms-inline-flexbox;
  display: inline-flex;
  width: 100%;
}

.board_section{
	border: 2px solid black;
	border-right: none;
	width:50%;
	min-width: 300px;
	height:100vh;
	background-color: #5F8C2F;
}

/*basic setup for board containig fires*/
.board_fire_basic{
    width: 49%; /*default = 49%*/
    height: 100vh; /*default = 100vh*/
	position: absolute; /*default = absolute*/
}

/*basic setup for board containig monsters*/
.board_monster_basic{
    width: 49%; /*default = 49%*/
    height: 100vh; /*default = 100vh*/
	position: absolute; /*default = absolute*/
}

.control_section{
	border: 2px solid black;
	border-right: none;
	width:50%;
	min-width: 300px;
	height:100vh;
	background-image: url('./img/bg1.jpg');
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
}

	.control_container{
		margin-top:2rem;
		text-align: center;
	}

		.control_card{
			background: rgba(0,0,0,0.5);
			width: 80%;
			height: 80vh;
			margin:0 auto;
		}


			.control_input{
				width:80%;
			}

			.control_submit{
				display: -ms-flexbox;
				display: flex;
				-ms-flex-pack: end;
				    justify-content: flex-end;
			}

/***********************************************************/
/***********************************************************/
/***********************************************************/
/*******************Subcomponents CSS***********************/
/***********************************************************/
/***********************************************************/

				
				.control_question {
					color: white;
					font-weight: 400;
					margin: auto;
					}

					.control_mission {
					width: 80%;
					padding: 1rem;
					margin: 0 auto;
					text-align: left;
					word-spacing: 0.2rem;
					}

					.control_form {
					background: rgba(0,0,0,0.6);
					width: 80%;
					padding: 1rem;
					margin: 0 auto;
					text-align: left;
					word-spacing: 0.2rem;
					}

					.hint{
						margin: 0;
						font-size: 0.7rem;
					    color: dodgerblue;
					}


